.footer{
  position: relative;
  padding: 60px 0;
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    @include size(100%);
    z-index: -1;
    & img{
      @include size(100%);
      object-fit: cover;
    }
  }
  & .container{
    display: flex;
    justify-content: space-between;
    &:after{
      display: none;
    }
    @media screen and (max-width: 768px){
      flex-direction: column;
      align-items: center;
    }
  }
  .logo{
    & svg{
      @include size(100%);
    }
  }
  &__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    & ul{
      display: flex;
      align-items: center;
      margin: 20px 0 0;
      & li{
        margin: 0 15px;
        @media screen and (max-width: 768px){
            &:last-of-type{
              position: relative;
              right: 10px;
            }
        }
      }
      & a{
        display: flex;
        align-items: center;
        @include size(24px);
        transition: $default-transition;
        & svg{
          @include size(24px)
        }
        &:hover{
          transform: scale(1.2);
        }
      }
    }
  }
  &__tel{
    display: grid;
    grid-template-columns: 40px auto;
    grid-template-rows: repeat(2, 1fr);
    align-items: center;
    text-align: right;
    @media screen and (max-width: 768px){
      margin: 20px 0;
    }
    & a{
      font-size: 17px;
      line-height: 21px;
      color: #fff;
      transition: $default-transition;
      transform: translateY(0) scale(1);
      &:nth-of-type(1):hover{
        transform: translateY(-10px) scale(1.3);
      }
      &:nth-of-type(2):hover{
        transform: translateY(10px) scale(1.3);
      }
    }
    & span {
      display: block;
      grid-row: 1/3;
      @include size(30px);
      & svg{
        @include size(100%);
      }
    }
  }
  &__contacts{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__rights{
    font-size: 12px;
    line-height: 15px;
    text-align: right;
    color: #FFFFFF;
  }
}